body {
	font-size: 10pt;
	line-height: 120%;
}

.title {
	font-size: 16pt;
	font-weight: bold;
}
p {
	margin: 0;
	padding: 0;
}

p {
	display: block;
	margin-block-start: 1em;
	margin-block-end: 1em;
	margin-inline-start: 0px;
	margin-inline-end: 0px;
}

.section {
	font-size: 12pt;
	font-weight: bold;
}
a:-webkit-any-link {
	color: -webkit-link;
	cursor: pointer;
	text-decoration: underline;
}
